import {Injectable} from "@angular/core";
import {Http, Request, RequestMethod} from "@angular/http";
import {Observable} from "rxjs/Observable";
import 'rxjs/Rx';

@Injectable()
export class RestDatasourceService {

  constructor(private http: Http) {}

  get(url: string, params?: {param:string,value: string}[]): Observable<any> {
    let queryParam :URLSearchParams = null;
    if(params) {
      for(let p of params) {
        queryParam.set(p.param, p.value);
      }
    }
    return this.request(RequestMethod.Get, url, queryParam);
  }


  request(method: RequestMethod, url: string, params?: URLSearchParams, body?: any): Observable<any> {
    let req: Request = new Request({
      method: method,
      url: url,
      params: params,
      body: body
    });
    return this.http.request(req).map(res => res.json());
  }
}



